.bar {
  transition: all 0.25s ease;
  height: 2px;
  background: #fff;
  position: absolute; }

.top {
  top: 0;
  width: 50%; }

.middle {
  width: 100%;
  top: 5px; }

.bottom {
  width: 50%;
  bottom: 6px; }

.half {
  width: 50%;
  bottom: 1px; }

.left {
  left: 0; }

.right {
  right: 0; }

.top.bar.left {
  -webkit-transform-origin: 0% 0%;
  -moz-transform-origin: 0% 0%;
  -ms-transform-origin: 0% 0%;
  -o-transform-origin: 0% 0%;
  transform-origin: 0% 0%; }

.top.bar.right {
  -webkit-transform-origin: 100% 0%;
