  -o-transform: translate(2px, -2px) rotate(45deg) scaleX(1.25);
  transform: translate(2px, -2px) rotate(45deg) scaleX(1.25); }

.nav-toggle-outer.active .top.bar.right {
  -webkit-transform: translate(-2px, -2px) rotate(-45deg) scaleX(1.25);
  -moz-transform: translate(-2px, -2px) rotate(-45deg) scaleX(1.25);
  -ms-transform: translate(-2px, -2px) rotate(-45deg) scaleX(1.25);
  -o-transform: translate(-2px, -2px) rotate(-45deg) scaleX(1.25);
  transform: translate(-2px, -2px) rotate(-45deg) scaleX(1.25); }

.nav-toggle-outer.active .bottom.bar.left {
  -webkit-transform: translate(2px, 8px) rotate(-45deg) scaleX(1.25);
  -moz-transform: translate(2px, 8px) rotate(-45deg) scaleX(1.25);
  -ms-transform: translate(2px, 8px) rotate(-45deg) scaleX(1.25);
  -o-transform: translate(2px, 8px) rotate(-45deg) scaleX(1.25);
  transform: translate(2px, 8px) rotate(-45deg) scaleX(1.25); }

.nav-toggle-outer.active .bottom.bar.right {
  -webkit-transform: translate(-2px, 8px) rotate(45deg) scaleX(1.25);
  -moz-transform: translate(-2px, 8px) rotate(45deg) scaleX(1.25);
  -ms-transform: translate(-2px, 8px) rotate(45deg) scaleX(1.25);
